.neomarphismui {
  width: 50%;
  border: 1px solid red;
  height: auto;
  margin: auto;
}

.neumorphic {
  box-shadow: 12px 12px 24px 0 rgba(0, 0, 0, 0.2),
    -12px -12px 24px 0 rgba(255, 255, 255, 0.5);
  font-size: 5rem;
  /* width: 200px; */
  height: 60px;
  border-radius: 40px;
  overflow: hidden;
  padding: 1.3rem;
  display: flex;
  flex-direction: column-reverse;
}

.variation2 {
  justify-content: space-around;
  align-items: center;
}
.variation2.pressed {
  color: #5ab9ea;
  box-shadow: inset 6px 6px 10px 0 rgba(0, 0, 0, 0.2),
    inset -6px -6px 10px 0 rgba(255, 255, 255, 0.5);
}
.variation2.green {
  color: green;
  box-shadow: inset 6px 6px 10px 0 rgba(0, 0, 0, 0.2),
    inset -6px -6px 10px 0 rgba(255, 255, 255, 0.5);
}
.variation2.brown {
  color: brown;
  box-shadow: inset 6px 6px 10px 0 rgba(0, 0, 0, 0.2),
    inset -6px -6px 10px 0 rgba(255, 255, 255, 0.5);
}
.variation2.orange {
  color: orange;
  box-shadow: inset 6px 6px 10px 0 rgba(0, 0, 0, 0.2),
    inset -6px -6px 10px 0 rgba(255, 255, 255, 0.5);
}
.variation2.red {
  color: red;
  box-shadow: inset 6px 6px 10px 0 rgba(0, 0, 0, 0.2),
    inset -6px -6px 10px 0 rgba(255, 255, 255, 0.5);
}
.variation2.blue {
  color: blue;
  box-shadow: inset 6px 6px 10px 0 rgba(0, 0, 0, 0.2),
    inset -6px -6px 10px 0 rgba(255, 255, 255, 0.5);
}
.variation2 span:first-child {
  display: block;
  /* padding-bottom: 1.5rem; */
  font-size: 1.3rem;
}
.variation2 span:last-child {
  display: block;
  /* padding-bottom: 1.5rem; */
  font-weight: 800;
  font-size: 0.6rem;
}
.variation2 span.small {
  display: block;
  /* padding-bottom: 1.5rem; */
  position: relative;
  font-size: 1rem;
}

